   clear: both;\n}\n.Recommend[data-v-d197ac90]{\n   height: 200px;\n   overflow: auto;\n}\n.Recommend>div[data-v-d197ac90]{\n   width: calc(100%/3 - 10px);\n   float: left;\n   height: 100%;\n   border-right: 1px solid #ccc;\n}\n.Recommend>div>p[data-v-d197ac90]:first-child{\n   width: 100%;\n}\n.Recommend>div[data-v-d197ac90]:last-child{\n   border: none;\n}\n.Recommend .el-radio[data-v-d197ac90]{\n     margin-left: 0;\n     display: block ;\n     margin-top: 10px\n}\n.enlarge>img[data-v-d197ac90]{\n   width: 50px;\n   height: 50px;\n   vertical-align: middle\n}\n", "", {"version":3,"sources":["E:/仲达集团/总后台管理系统/Zht-master/src/pages/serviceManager/materielApply/register/src/pages/serviceManager/materielApply/register/register.vue"],"names":[],"mappings":";AAwWA;GACA,wBAAA;GACA,iBAAA;CACA;AACA;GACA,gBAAA;CAEA;CACA,MAAA;AACA;GACA,YAAA;GACA,YAAA;GACA,mBAAA;GACA,uBAAA;GACA,eAAA;CACA;CACA,UAAA;AACA;GACA,mBAAA;CAEA;AACA;GACA,YAAA;GACA,YAAA;GACA,mBAAA;GACA,mBAAA;GACA,iBAAA;CACA;CACA,UAAA;AACA;IACA,iBAAA;IACA,oBAAA;CACA;AACA;GACA,gBAAA;GACA,iCAAA;CACA;AACA;GACA,sBAAA;GACA,WAAA;CACA;AACA;GACA,kBAAA;CACA;AACA;GACA,kBAAA;CACA;CACA,QAAA;AACA;IACA,oBAAA;CACA;AACA;GACA,iBAAA;GACA,mBAAA;CACA;AACA;IACA,sBAAA;IACA,aAAA;CAEA;AACA;GACA,aAAA;GACA,YAAA;GACA,+BAAA;GACA,qCAAA;GACA,mBAAA;GACA,mBAAA;CACA;AACA;GACA,YAAA;GACA,YAAA;GACA,+BAAA;GACA,qCAAA;GACA,mBAAA;GACA,6BAAA;QAAA,wBAAA;CACA;CACA,SAAA;AACA;GACA,YAAA;CACA;AACA;GACA,aAAA;GACA,uBAAA;GACA,YAAA;GACA,cAAA;CACA;AACA;GACA,YAAA;GACA,mBAAA;CACA;AACA;KACA,YAAA;CACA;AACA;GACA,eAAA;GACA,YAAA;GACA,YAAA;CACA;AACA;GACA,cAAA;GACA,eAAA;CACA;AACA;GACA,2BAAA;GACA,YAAA;GACA,aAAA;GACA,6BAAA;CAEA;AACA;GACA,YAAA;CAEA;AACA;GACA,aAAA;CACA;AACA;KACA,eAAA;KACA,gBAAA;KACA,gBAAA;CACA;AACA;GACA,YAAA;GACA,aAAA;GACA,sBAAA;CACA","file":"register.vue","sourcesContent":["<template lang=\"html\">\n    <div class=\"register\" ref='register'>\n        <table class=\"table\">\n            <tr>\n                <th>姓名</th>\n                <th>电话</th>\n                <th>头像</th>\n                <th>详情</th>\n                <th>支付凭证</th>\n                <th>注册地区</th>\n                <th>时间</th>\n                <th>操作</th>\n            </tr>\n            <tr v-for=\"(item,index) in dataList\">\n                <td>{{item.name}}</td>\n                <td>{{item.phone}}</td>\n                <td>\n                    <img :src=\"item.head_pic\" class=\"head_pic\">\n                </td>\n                <td>\n                    <a href=\"javascript:;\" class=\"col-1EA\" @click=\"SeeDetails(item.uid)\">查看</a>\n                </td>\n                <td>\n                <div class='enlarge' >\n                    <img :src='item.voucher' ref='enlarge' @click='enlarge(index)' />\n                </div>\n                </td>\n                <td>\n                    <a href=\"javascript:;\" class=\"col-1EA\" @click='regionDialogVisible=true,region(item.id)'>查看</a>\n                </td>\n                <td>\n                    <span>{{item.create_time}}</span>\n                </td>\n                <td>\n                    <a href=\"javascript:;\" class=\"col-0ACD00 adopt\" @click=\"uid=item.uid,adopt(item.id)\">通过</a>\n                    <a href=\"javascript:;\" class=\"col-FA644B\" @click=\"Reject(item.id)\">驳回</a>\n                </td>\n            </tr>\n        </table>\n\n        <!--详情弹框  -->\n        <el-dialog title=\"详情\" :visible.sync=\"detailsDialogVisible\" width=\"400px\" center>\n            <div class='Head_portrait'>\n                <img :src=\"detailsData.head_pic\" >\n            </div>\n            <div class=\"content\">\n                <div>\n                    <span>姓名:</span>\n                    <span>{{detailsData.name}}</span>\n                </div>\n                <div>\n                    <span>电话:</span>\n                    <span>{{detailsData.phone}}</span>\n                </div>\n                <div>\n                    <span>开户名:</span>\n                    <span>{{detailsData.bank_name}}</span>\n                </div>\n                <div>\n                    <span>开户行:</span>\n                    <span>{{detailsData.bank_code}}</span>\n                </div>\n                <div>\n                    <span>分行:</span>\n                    <span>{{detailsData.bank_branch}}</span>\n                </div>\n                <div>\n                    <span>卡号:</span>\n                    <span>{{detailsData.account}}</span>\n                </div>\n            </div>\n        </el-dialog>\n\n        <!--通过申请弹框-->\n        <el-dialog title=\"通过申请\" :visible.sync=\"adoptDialogVisible\" width=\"400px\" center>\n            <div class=\"adoptContent\">\n               \n                <div>\n                    <span>注册类型:</span>\n                    <span>{{adoptData.sm_type}}</span>\n                </div>\n                <div>\n                    <span>管理奖励分成:</span>\n                    <span>\n                        <input class='input' v-model='adoptData.sm_profit' />\n                        <span>%</span>\n                    </span>\n                </div>\n                <div >\n                    <span>ubi奖励分成:</span>\n                    <span>\n                        <input class='input' v-model='adoptData.ubi_profit' />\n                        <span>元</span>\n                    </span>\n                </div>\n                <div>\n                    <a href=\"javascript:;\" class=\"col-1EA\" @click='RecommendVisible=true,RecommendData(adoptData.pid)'>推荐人: {{Recommender}} <span v-show='!radio'> (请点击选择)</span></a>\n                </div>\n                <div v-show='radio'>\n                    <span>推荐奖励:</span>\n                    <span>\n                        <input class='input' v-model='adoptData.price' placeholder='请输入' />\n                        <span>元</span>\n                    </span>\n                 </div>\n            </div>\n            <el-dialog title=\"推荐人\" append-to-body :visible.sync=\"RecommendVisible\" width=\"30%\" center>\n                <div class='Recommend'>\n                    <div>\n                        <p>服务经理</p>\n                        <p>\n                            <el-radio-group v-model=\"radio.name\">\n                                <el-radio @click.native.prevent=\"changeRadio(item)\" v-for='item in serveList' :label=\"item.name\" :key='item.name'>{{item.name}}</el-radio>\n                            </el-radio-group>\n                        </p>\n                    </div>\n                </div>\n                <div slot=\"footer\" class=\"dialog-footer\">\n                    <el-button type=\"primary\" @click=\"Sure\">确定</el-button>\n                </div>\n            </el-dialog>\n            <div slot=\"footer\" class=\"dialog-footer\">\n                <el-button type=\"primary\" @click=\"DetermineAdopt\">确定</el-button>\n            </div>\n        </el-dialog>\n\n        <!--驳回弹框-->\n        <el-dialog title=\"驳回理由\" :visible.sync=\"RejectDialogVisible\" width=\"400px\" center @close=\"RejectClose\">\n            <div class='textarea'>\n                <textarea v-model=\"reason\"></textarea>\n            </div>\n            <div slot=\"footer\" class=\"dialog-footer\">\n                <el-button type=\"primary\" @click=\"DetermineReject\">确定</el-button>\n            </div>\n        </el-dialog>\n    \n         <el-dialog title=\"地区\" :visible.sync=\"regionDialogVisible\" width=\"400px\" center >\n            <div >\n                <div>\n                    <strong style='font-size:16px'>省: </strong>\n                    <span v-for='item in reasonData'>\n                        {{item.name}}\n                        <div>\n                            <strong style='font-size:16px'>市: </strong>\n                            <span v-for='it in item.son'>\n                                {{it.name}}\n                                <div>\n                                    <strong style='font-size:16px'>区/县: </strong>\n                                    <span v-for='i in it.son'>{{i.name}}、</span>\n                                </div>\n                            </span>\n                        </div>\n                      \n                    </span> \n                </div>\n                \n            </div>\n           \n        </el-dialog>\n        \n        <!--分页的组件-->\n        <div class=\"page_center\">\n         <paging :page-count=\"pageCount\"   :page=\"page\" @index=\"paging\" ></paging>\n        </div>\n    </div>\n</template>\n\n<script>\n  import news from \"@/components/news/news\"\n  import Viewer from '@/utils/Viewer'\n  export default {\n    data(){\n      return{\n        token:window.sessionStorage.getItem(\"bbytoken\"),\n        pageCount:1,  //总页数,默认1\n        page:1,       //当前页\n        dataList:[],\n        detailsDialogVisible:false,  //详情弹框,默认不显示\n        detailsData:{},  //详情弹框数据\n        adoptDialogVisible:false,    //通过申请弹框,默认不显示\n        adoptData:{},//通过弹框数据\n        selectData:'', //下拉数据\n        adoptID:'', //通过id,\n        RejectID:'',//驳回id\n        RejectDialogVisible:false,   //驳回弹框,默认不显示\n        reason:'', //驳回理由,\n        ImgDialogVisible:false,\n        imgUrl:'',\n        RecommendVisible:false,\n        serveList:'', //服务经理\n     \n        radio:'',\n        regionDialogVisible:false,\n        reasonData:[],\n        check:false\n      }\n    },\n    created(){\n        \n    },\n    methods:{\n      //初始化数据\n      init(){  //调取注册审核列表接口\n        this.$axios.post('Smoc/regAudit',{token:this.token,page:this.page}).then(res=>{\n          if(res.data.code==1){\n        \n          this.dataList=res.data.data.data     //拿返回来的数据\n          this.pageCount=res.data.data.last_page  //拿取总页数\n        }else {\n          this.dataList=null\n          layer.msg(res.data.msg,{time:1000}) //如果code不等于一代表,没有数据，提示暂无数据\n        }\n      }).catch(err=>console.log(err))\n      },\n      /** 支付凭证放大 **/\n      enlarge(index){\n     \n          this.$nextTick(()=>{\n             const ViewerDom=this.$refs.enlarge[index];\n            Viewer(ViewerDom)\n          })\n      },\n      /** **/\n      changeRadio(e){\n      \n        this.radio = e.name===this.radio.name ? '' :e \n      },\n      /** 地区 */\n      region(id){\n         this.$axios.post('admin/Smoc/areaNum',{\n           token:this.token,\n           id:id,\n           type:1\n         }).then(res=>{\n             if(res.data.code==1){\n               this.reasonData=res.data.data;\n             }\n         }).catch(err=>console.log(err))\n      },\n      //操作分页组件\n      paging(e){\n        //把当前页赋值给page\n        this.page=e;\n        // 当前页发生改变时调取注册审核列表接口,重新渲染\n        this.init()\n      },\n      //点击查看详情\n      SeeDetails(uid){\n        this.$axios.post('Smoc/regDetail',{token:this.token,uid:uid}).then(res=>{\n          if(res.data.code==1){\n              this.detailsDialogVisible=true\n          \n              this.detailsData=res.data.data;\n          }else{\n             layer.msg(res.data.msg,{time:1000})\n          }\n      }).catch(err=>console.log(err))\n      },\n      //点击通过\n      adopt(id){\n        this.adoptDialogVisible=true;\n        this.adoptID=id;\n        this.$axios.post('Smoc/checkREg',{token:this.token,id:id}).then(res=>{\n            if(res.data.code==1){\n               \n                 this.adoptData=res.data.data;\n                 if(this.adoptData.sm_status==1)this.selectData=1\n                 if(this.adoptData.sm_status==2)this.selectData=2\n            }\n        }).catch(err=>console.log(err))\n      },\n      // 点击确定通过\n      DetermineAdopt(){\n         var adoptData={\n           token:this.token,\n           id:this.adoptID,\n           sm_profit:this.adoptData.sm_profit,\n           sm_mold:this.selectData,\n           share_id:this.radio.sm_id,\n           ubi_profit:this.adoptData.ubi_profit,\n           price:this.adoptData.price\n         }\n          this.$axios.post('Smoc/regPass',adoptData).then(res=>{\n              if(res.data.code==1){\n                 layer.msg(res.data.msg,{time:1500},(()=>{\n                       this.$store.commit('increment')\n                       this.adoptDialogVisible=false\n                       this.$router.push('/serviceManager/Physics')\n                   }))\n              }else{\n                \n                 layer.msg(res.data.msg,{time:1500})\n              }\n          }).catch(err=>console.log(err))\n      },\n      RecommendData(pid){\n         this.$axios.post('Smoc/getListPersonnel',{token:this.token,area_id:pid,sm_id:this.uid}).then(res=>{\n             if(res.data.code==1){\n         \n                this.serveList=res.data.data\n              \n             }\n         }).catch(err=>console.log(err))\n      },\n      Sure(){\n         this.RecommendVisible=false\n      },\n      //点击驳回\n      Reject(id){\n        this.RejectDialogVisible=true\n        this.RejectID=id;\n      },\n      //点击关闭弹框情况文本域中的内容\n      RejectClose(){\n         this.reason=''\n      },\n      //点击确认驳回\n      DetermineReject(){\n         if(!this.reason){  //如果没有输入驳回理由,直接return,不让执行下面的操作\n            this.$message({message:'请输入驳回理由!',type:'error'})\n            return;\n         }\n          this.$axios.post('Smoc/regDown',{token:this.token,id:this.RejectID,reason:this.reason}).then(res=>{\n              if(res.data.code==1){\n                  layer.msg(res.data.msg,{time:1500},(()=>{\n                      this.$store.commit('increment')\n                      this.RejectDialogVisible=false\n                      this.init()\n                    }))\n              }else{\n                 layer.msg(res.data.msg,{time:1500})\n              }\n          }).catch(err=>console.log(err))\n      }\n    },\n    computed:{\n      count(){\n        return this.$store.state.count\n      },\n      /** 推荐人 -- 检测是否是不是本地地区 **/\n      Recommender(){\n         let radio=this.radio;\n         return radio ? `${radio.name}( ${radio.sign==1 ? '本地区' : '不是本地区' } )` : '暂无'\n      },\n    //   /** 检测是本地区开发奖励不是本地区推荐奖励 **/\n    //   Testing_region(){\n    //     let radio=this.radio;\n    //     return radio.sign ? '开发奖励' : '推荐奖励'\n    //   }\n    },\n    mounted(){\n      this.init()\n      this.$store.commit('increment')\n      \n    }\n  }\n</script>\n\n<style lang=\"css\" scoped=\"scoped\">\n\n  .table>tr>th{\n    height: 50px !important;\n    line-height: 50px\n  }\n  .table>tr>td{\n    font-size: 14px;\n\n  }\n  /*头像*/\n  .head_pic{\n    width: 50px;\n    height:50px;\n    border-radius: 50%;\n    vertical-align: middle;\n    margin: 17px 0;\n  }\n  /*详情弹框头像*/\n  .Head_portrait{\n    text-align: center;\n\n  }\n  .Head_portrait>img{\n    width: 80px;\n    height:80px;\n    border-radius: 50%;\n    text-align: center;\n    margin-top: 20px;\n  }\n  /*详情弹框内容*/\n  .content{\n     margin-top: 40px;\n     margin-bottom: 50px;\n  }\n  .content>div{\n    padding: 12px 0;\n    border-bottom: 1px solid #E6E6EB;\n  }\n  .content>div>span{\n    display: inline-block;\n    width: 49%;\n  }\n  .content>div>span:last-child{\n    text-align: right;\n  }\n  .adopt{\n    margin-right: 29px\n  }\n  /*通过弹框*/\n  .adoptContent{\n     margin-bottom: 38px;\n  }\n  .adoptContent>div{\n    margin-top: 20px;\n    padding-left: 20px;\n  }\n  .adoptContent>div>span:first-child{\n     display: inline-block;\n     width: 100px;\n\n  }\n  .input{\n    width: 120px;\n    height:40px;\n    background:rgba(245,245,250,1);\n    border:1px solid rgba(230,230,235,1);\n    margin-right: 10px;\n    text-align: center;\n  }\n  .select{\n    width:120px;\n    height:40px;\n    background:rgba(245,245,250,1);\n    border:1px solid rgba(230,230,235,1);\n    text-align: center;\n    text-align-last: center;\n  }\n  /*驳回弹出框*/\n  .textarea{\n    width: 100%;\n  }\n  .textarea>textarea{\n    border: none;\n    width: 100% !important;\n    resize:none;\n    height: 100px;\n  }\n  .imgUrl{\n    width: 100%;\n    text-align: center;\n  }\n  .imgUrl>img{\n      width: 100%;\n  }\n  .Recommend:after{\n    display: block;\n    content: '';\n    clear: both;\n  }\n  .Recommend{\n    height: 200px;\n    overflow: auto;\n  }\n  .Recommend>div{\n    width: calc(100%/3 - 10px);\n    float: left;\n    height: 100%;\n    border-right: 1px solid #ccc;\n\n  }\n  .Recommend>div>p:first-child{\n    width: 100%;\n \n  }\n  .Recommend>div:last-child{\n    border: none;\n  }\n .Recommend .el-radio{\n      margin-left: 0;\n      display: block ;\n      margin-top: 10px\n }\n  .enlarge>img{\n    width: 50px;\n    height: 50px;\n    vertical-align: middle\n  }\n</style>\n<style>\n   .el-dialog__header{\n    border-bottom: 1px solid #E6E6EB !important;\n    padding: 13px 0 !important;\n    background:#fff !important;\n    color:#f5f5;\n  }\n  .el-dialog__headerbtn{\n    top: 15px;\n  }\n  .el-dialog__footer{\n    border-top: 1px solid #E6E6EB;\n    padding-bottom: 10px;\n  }\n</style>\n"],"sourceRoot":""}]);

